html {
  overflow: hidden;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

#root {
  height: 100vh;
  width: 100vw;
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

span {
  font-family: "Noto Sans HK" sans-serif, "Noto Sans Lao Looped", sans-serif;
}

@keyframes vertical-rolling {
  0% {
    transform: rotate(270deg);
    font-size: 83vw;
    line-height: 1;
    min-width: 100vh;
  }
  100% {
    transform: rotate(270deg) translateX(calc(-100vh - 100%));
    font-size: 83vw;
    line-height: 1;
    min-width: 100vh;
  }
}

@keyframes horizontal-rolling {
  0% {
    transform: translateX(100vw);
    font-size: 83vh;
    line-height: 1;
    min-width: 100vw;
  }
  100% {
    transform: translateX(-100%);
    font-size: 83vh;
    line-height: 1;
    min-width: 100vw;
  }
}
